@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff") format("woff"), url("../fonts/oswaldregular.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff") format("woff"), url("../fonts/oswaldmedium.woff2") format("woff2");
  font-style: normal;
  font-weight: 500;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
body {
  font-family: "Oswald", "Arial", sans-serif, "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  color: #111111;
}
a {
  text-decoration: none;
  color: inherit;
}
.button {
  font-family: inherit;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
  background-color: #68b738;
  color: #ffffff;
  border: none;
  cursor: pointer;
  display: block;
  margin: 0 auto;
  padding: 11px 20px;
  text-align: center;
  box-sizing: border-box;
}
.button:hover {
  background-color: #5da332;
}
.button:active {
  background-color: #5da332;
  color: #91d16a;
}
@media (min-width: 768px) {
  .button--tall {
    padding: 18px 20px;
  }
}
